
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

* {
  font-family: 'Roboto', sans-serif;
}

.info1, .info2{
  font-size: calc(8px + 1vh);
  font-weight: 1000;
  position: absolute;
  left: 1vh;
  border-radius: 5%;
  padding: 1vh;
}

.info1{
  background-color: rgba(255, 255, 255, 0);
  color: rgba(0, 0, 0, 0);
  transition-duration: 200ms;
  z-index: 2;
}

.info2{
  background-color: rgba(255, 255, 255, 0.219);
  color: rgb(0, 0, 0);
  pointer-events: none;
  z-index: 1;
}

.info1:hover{
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}



.App {
  z-index: 2;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  background: linear-gradient(130deg, rgb(0, 255, 200) 0%, rgb(229, 255, 0) 35%, rgb(255, 0, 191) 100%);
  overflow: hidden;
  align-items: stretch;
  align-content: stretch;
  justify-content: stretch;
}

.App:active > .square1 {
  background-color: rgb(0, 0, 0);
  width: 10vh;
  height: 10vh;
  transition: border-radius 500ms ease, background-color 500ms ease;
  border-radius: 30%;
}

[class^="square"]{
  background-color: rgb(0, 0, 0);
  width: 10vh;
  height: 10vh;
  transition: border-radius 5000ms ease;
}

[class^="square"]:hover{
  background-color: rgb(0, 0, 0);
  width: 10vh;
  height: 10vh;
  transition: border-radius 500ms ease, background-color 500ms ease;
  border-radius: 30%;
}

[class^="square"]:active{
  background-color: rgba(0, 0, 0, 0);
}
